import React from 'react';
import { TabBar } from 'antd-mobile';

const Detail = (props) => {
    return (
        <div style={{display: 'flex', height: '100vh', flexDirection: 'column', justifyContent: 'space-between'}}>
            <div style={{height: '300px', backgroundSize: '100% 100%', backgroundPosition: '50% 50%', padding: '16px', backgroundImage: 'url(' + new URL(window.location).searchParams.get("img") + ')'}}>
                <a onClick={() => props.history.push('/')} style={{display: 'block', height: '24px', width: "24px", backgroundColor: 'rgba(0,0,0,.5)', borderRadius: '50%', color: "#fff", boxSizing: 'border-box', padding: '4px 6px'}}>{"<"}</a>
            </div>
            <div style={{bottom:0,position:'fixed',width:'100%',backgroundColor:'rgb(255,255,255)'}}>
                <div style={{width: '40%',bottom: 0,float:'left'}}>
                    <TabBar
                        unselectedTintColor="#949494"
                        tintColor="grey"
                        barTintColor="white"
                    >
                        <TabBar.Item
                            title="店铺"
                            key="store"
                            icon={
                                <i className='iconfont icon-dianpu'></i>                           
                            }
                            selectedIcon={
                                <i className='iconfont icon-dianpu1'></i>                           
                            }
                            onPress={ () => {
                        
                            } }
                        >
                        </TabBar.Item>
                        <TabBar.Item
                            icon={
                                <i className='iconfont icon-wangwang1'></i>                           
                            }
                            selectedIcon={
                                <i className='iconfont icon-wangwang'></i>                           
                            }
                            title="客服"
                            key="server"
                            onPress={ () => {
                                
                            } }
                        ></TabBar.Item>
                        <TabBar.Item
                            icon={
                                <i className='iconfont icon-shoucang'></i>
                            }
                            selectedIcon={
                                <i className='iconfont icon-shoucang1'></i>                           
                            }
                            title="收藏"
                            key="collect"
                            onPress={ () => {
                                
                            } }
                        > 
                        </TabBar.Item>
                    </TabBar>
                </div>
                <button style={{height:'40px',border:'0px',marginTop:'5px',width:'30%', borderRadius: '1rem 0 0 1rem', backgroundImage: 'linear-gradient(to right, #FFC500, #FF9402)', float: 'left',padding: '6px 16px', color: '#fff', fontWeight: 'bold'}}>加入购物车</button>
                <button style={{height:'40px',border:'0px',marginTop:'5px',width:'30%',borderRadius: '0 1rem 1rem 0', backgroundImage: 'linear-gradient(to right, #FF7A00, #FE560A)', float: 'float',padding: '6px 16px', color: '#fff', fontWeight: 'bold'}}>立即购买</button>
            </div>
        </div>
    )
}

export default Detail;
